@use '_colors';
@use '_fonts';

// border-box
*,
*::before,
*::after {
	// margin: 0;
	// padding: 0;
	box-sizing: border-box;
}

:root {
	--border-radius: 0.25em;
	--card-shadow-blur: 1.5em;
}

h1,
h2,
h3 {
	font-family: fonts.$serif;
}

code {
	color: var(--vscode-editor-foreground);
}

body {
	color: var(--color-main);
	font-family: fonts.$sans-serif;
	font-size: var(--vscode-editor-font-size);

	--body-padding: 1rem;
	padding: var(--body-padding);

	--color-light: #{colors.$light};
	--color-dark: #{colors.$dark};
	--color-pill-active-background: var(--vscode-activityBarBadge-background);
	--color-pill-active-foreground: var(--vscode-activityBarBadge-foreground);

	&.vscode-light {
		--color-main: #{colors.$dark};
		--color-secondary: #{colors.$dark-secondary};
		--color-tertiary: #{colors.$dark-tertiary};
		--color-logo-shadow: rgba(74 91 94 / 50%);
		--color-card-shadow: rgba(25 30 31 / 10%);

		--color-pill-background: #{colors.$light-inactive};
		--color-pill-foreground: #{transparentize(colors.$dark, 0.5)};
	}

	&.vscode-dark {
		--color-main: #{colors.$light};
		--color-secondary: #{colors.$light-secondary};
		--color-tertiary: #{colors.$light-tertiary};
		--color-logo-shadow: rgba(255 255 255 / 17.5%);
		--color-card-shadow: rgba(25 30 31 / 40%);

		--color-pill-background: #{colors.$dark-inactive};
		--color-pill-foreground: #{transparentize(colors.$light, 0.5)};
	}

	&.vscode-high-contrast {
		color: red;
	}
}

.text-align-left {
	text-align: left;
}
.text-align-center {
	text-align: center;
}
.text-align-right {
	text-align: right;
}
